import React, { createContext, useState, useContext, useEffect } from 'react';
import { getProfileDashboard, initializeTelegramAuth } from '../services/api';
import { getTelegramUserData } from '../utils/telegramUtils';

const AuthContext = createContext();

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const initUser = async () => {
      try {
        const storedUser = localStorage.getItem('user');
        if (storedUser) {
          setUser(JSON.parse(storedUser));
        } else {
          const telegramData = await getTelegramUserData();
          if (telegramData) {
            const authResult = await initializeTelegramAuth(telegramData);
            if (authResult.token) {
              localStorage.setItem('token', authResult.token);
              const userData = await getProfileDashboard();
              setUser(userData);
              localStorage.setItem('user', JSON.stringify(userData));
            }
          }
        }
      } catch (error) {
        console.error('Failed to initialize user:', error);
      } finally {
        setLoading(false);
      }
    };

    initUser();
  }, []);

  const updateUser = (userData) => {
    const updatedUser = { ...user, ...userData };
    setUser(updatedUser);
    localStorage.setItem('user', JSON.stringify(updatedUser));
  };

  return (
    <AuthContext.Provider value={{ user, setUser, updateUser, loading }}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => useContext(AuthContext);